import axios from 'axios'

export function getYoutubeIdByUrl(url) {
    const regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#/&/?]*).*/;
    const match = url.match(regExp);

    if (match && match[7].length == 11) {
        return match[7];
    }

    return false;
}

 export async function getVideoData (id = false) {
    if (id) {
        const instance = axios.create();
        delete instance.defaults.headers.common['Authorization'];
        const token = 'AIzaSyBKJmZe9L3Y_Z03b5wbldADYzTGpvSWGUM'
        const res = await instance.get(`https://www.googleapis.com/youtube/v3/videos?part=snippet&key=${token}&id=${id}`)
        return res.data
    } else {
        return null
    }
}