<template>
    <div class="page-content">
        <div class="page-block order-statuses-wrapper mb30 page-padding-y">
            <div v-if="width > 900" class="order-statuses flex-start">
                <div v-for="s in statuses" :key="s.key"
                     :class="[{'order-status-active': s.key == status.key}, 'order-status']"
                     @click="status = s">
                    <span>{{s.title}}</span>
                </div>
            </div>
            <div v-else class="order-statuses">
                <div ref="sort_block" class="sort-block" @click="show_dropdown = !show_dropdown">
                    <span class="current">{{status.title}}</span>
                    <span class="icon"></span>
                    <ul class="dropdown"
                        v-if="show_dropdown"
                        v-click-outside="{
                          exclude: ['sort_block'],
                          handler: 'onClose'
                        }">
                        <li v-for="s in statuses" :key="s.key" @click="status = s">{{s.title}}</li>
                    </ul>
                </div>
            </div>

            <div class="order-filters flex-between">
                <div class="input-wrap-short">
                    <label>ID</label>
                    <input type="text" placeholder="2015345" v-model="order_id">
                </div>
                <div class="input-wrap-short">
                    <label>Ссылка на ролик</label>
                    <input type="text" placeholder="https://www.youtube.com/" v-model="video_url">
                </div>
                <button class="button-gradient">Применить</button>
            </div>
        </div>

        <div class="page-block orders-block">
            <div class="order-item flex-between" v-for="order in orders" :key="order.id">
                <div class="order__top" v-if="width <= 900">
                    <div class="order-item__data flex-start">
                        <div class="title">ID:</div>
                        <div class="value">{{order.id}}</div>
                    </div>
                    <div class="order-item__data flex-start">
                        <div class="title">Ссылка:</div>
                        <div class="value">{{order.video_url}}</div>
                    </div>
                    <div class="order-item__data flex-start">
                        <div class="title">Статус:</div>
                        <div :class="[
                            {'green': order.status == '1'},
                            {'red': order.status == '2'},
                            {'grey': order.status == '3'},
                            'value',
                        ]">{{statusString(order.status)}}
                        </div>
                    </div>
                </div>

                <div class="order__left">

                    <div class="order-title flex-start" v-if="width > 900">
                        <span>Ссылка на видео</span>
                        <a :href="order.video_url" target="_blank">{{order.video_url}}</a>
                    </div>

                    <div class="order__video flex-between" v-if="order.video_data">
                        <a target="_blank" :href="order.video_url" class="image">
                            <img :src="order.video_data.thumbnails.medium.url" alt="">
                        </a>
                        <div class="title">{{order.video_data.title}}</div>
                    </div>

                    <div class="order__data-mobile" v-if="width <= 900">
                        <div class="order-item__data flex-start">
                            <div class="title">Заказано:</div>
                            <div class="value">{{order.order_date}}</div>
                        </div>
                        <div class="order-item__data flex-start">
                            <div class="title">Сделано:</div>
                            <div class="value">{{order.order_finish}}</div>
                        </div>
                        <div class="order-item__data flex-start">
                            <div class="title">Стартовое значение:</div>
                            <div class="value">{{order.start_value}}</div>
                        </div>
                    </div>

                    <div class="order-refresh flex-start">
                        <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="28"
                             height="28" viewBox="0 0 28 28" fill="none">
                            <mask id="mask40" mask-type="alpha" maskUnits="userSpaceOnUse" x="0" y="0" width="28"
                                  height="28">
                                <rect width="28" height="28" fill="url(#pattern40)"/>
                            </mask>
                            <g mask="url(#mask40)">
                                <rect width="28" height="28" fill="#9B9B9B"/>
                            </g>
                            <defs>
                                <pattern id="pattern40" patternContentUnits="objectBoundingBox" width="1" height="1">
                                    <use xlink:href="#image40" transform="scale(0.00666667)"/>
                                </pattern>
                                <image id="image40" width="150" height="150"
                                       xlink:href="data:image/png;base64,"/>
                            </defs>
                        </svg>
                        <span>Обновить</span>
                    </div>

                    <div class="order-bottom flex-start">
                        <div class="price"><span>Стоимость </span><span>{{order.total_price}} руб.</span></div>
                        <button
                                @click="order.show_detail = !order.show_detail"
                                :class="[
                                {'button-hollow-active': order.show_detail},
                                'button',
                                'button-hollow'
                           ]">подробнее
                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="10" viewBox="0 0 16 10"
                                 fill="none">
                                <path d="M0 5H14M14 5L10 9.5M14 5L10 0.5" stroke="#FF3923" stroke-width="1.5"/>
                            </svg>
                        </button>
                    </div>
                </div>

                <div class="order__right" v-if="width > 900">
                    <div class="order-item__status flex-between">
                        <div class="title">Статус</div>
                        <div class="status">{{statusString(order.status)}}</div>
                        <div :class="[
                            {'green': order.status == '1'},
                            {'red': order.status == '2'},
                            {'grey': order.status == '3'},
                            'circle'
                        ]"></div>
                    </div>
                    <div class="order-item__data">
                        <div class="title">ID</div>
                        <div class="value">{{order.id}}</div>
                    </div>
                    <div class="order-item__data">
                        <div class="title">Заказано</div>
                        <div class="value">{{order.order_date}}</div>
                    </div>
                    <div class="order-item__data">
                        <div class="title">Сделано</div>
                        <div class="value">{{order.order_finish}}</div>
                    </div>
                    <div class="order-item__data">
                        <div class="title">Стартовое значение</div>
                        <div class="value">{{order.start_value}}</div>
                    </div>
                </div>

                <div v-if="order.show_detail" class="order__detail flex-between">
                    <div class="select-wrap-short">
                        <label>Страны</label>
                        <v-select class="select"
                                  :placeholder="`${order.countries[0]} и еще ${order.countries.length - 1}`"
                                  :from="order.countries" tagging :tag-keys="[9, 32, 188]"></v-select>
                    </div>
                    <div class="input-wrap-short">
                        <label>Источник</label>
                        <input type="text" readonly placeholder="Пустой" v-model="order.source">
                    </div>
                    <div class="input-wrap-short">
                        <label>Начало</label>
                        <input type="text" readonly v-model="order.order_activated">
                    </div>
                    <div class="input-wrap-short">
                        <label>Конец</label>
                        <input type="text" readonly v-model="order.order_finish">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {getYoutubeIdByUrl, getVideoData} from '../../scripts/youtube'

    export default {
        name: "Orders",
        props: ['width'],
        data: () => ({
            show_dropdown: false,
            status: {
                title: 'Все',
                key: 'all'
            },
            statuses: [
                {
                    title: 'Все',
                    key: 'all'
                },
                {
                    title: 'Активные',
                    key: 'active'
                },
                {
                    title: 'Завершенные',
                    key: 'finished'
                },
                {
                    title: 'Отмененные',
                    key: 'canceled'
                },
            ],
            order_id: null,
            video_url: '',
            orders: []
        }),
        methods: {
            statusString(code) {
                switch (code) {
                    case 1:
                        return 'Активен'
                    case 2:
                        return 'Отменен'
                    case 3:
                        return 'Завершен'
                    default:
                        return 'Не определен'
                }
            },
            onClose() {
                this.show_dropdown = false
            },
            async getOrders() {
                const res = await this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/client/orders/`,
                    method: 'GET'
                })
                this.orders = res.data.map(item => {
                    return {
                        video_url: item.video,
                        id: item.id,
                        order_date: new Date(item.date).toLocaleDateString(),
                        order_activated: new Date(item.date).toLocaleDateString(),
                        order_finish: '',
                        start_value: item.amount,
                        source: '',
                        countries: item.countries.split(';'),
                        show_detail: false,
                        video_data: null,
                        status: item.status,
                        total_price: item.total_price
                    }
                })
            },
        },
        created: async function () {
            window.scrollTo(0, 0)
            await this.getOrders()
            const video_ids = this.orders.reduce((acc, order) => {
                return [...acc, getYoutubeIdByUrl(order.video_url)]
            }, '').join(',')

            const res = await getVideoData(video_ids)

            for (let i = 0; i < res.items.length; i++) {
                this.orders[i].video_data = res.items[i].snippet
            }
        }
    }
</script>

<style scoped>

</style>